		kindToggle: function(e) {
			var id = e.currentTarget.dataset.id,list = this.list;
			for (var i = 0, len = list.length; i < len; ++i) {
				if (list[i].id == id) {
					list[i].open = !list[i].open
				} else {
					list[i].open = false
				}
			}
			this.data = list;
		}
	}
